// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

import proto from './basic/proto'
import filter from './basic/filter'

Vue.use(proto)
Vue.use(filter)

// 全局声明 SelfTable 组件， tableManage.js挂在window上减少组件内引用
// webpack2 vue 异步引用组件
Vue.component('SelfTable', () => import('@/components/part/table.vue'))
import tableManage from './basic/mixs/tableManage';
window.tableManage = tableManage;

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



